.newsletter{
    width: 100%;
    display: flex;
    align-items: center;
    height: 400px;
    background: #f9f9f9 url("../../../assets/newletter.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.newletter_content{
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.upper_text{
     margin-bottom: 20px;
     text-transform: uppercase;
     color: rgba(0,0,0, 0.7);
     font-weight: 900;
     font-size: 20px;
}
.lg_text{
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 30px;
    padding: 0 30px;
    font-weight: 700;
    color: white;
    line-height: 35px;
    text-align: center;

}
.form{
    display: flex;
    gap: 5px;
    margin-bottom: 10px;
}
.form input{
    width: 200px;
    height: 40px;
    border: 2px solid rgba(0, 0, 0, 0.9);
    padding: 0 5px;
    font-size: 15px;
    outline: none;
}
.form button{
    width: 100px;
    border: 0;
    outline: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 40px;
    background: red;
    color: white;
    font-size: 15px;
    font-weight: bold;
    border-bottom: 2px solid red;


}

.lower_text{
    margin-bottom: 20px;
    color: rgba(225,225,225,0.7);
    font-size: 15px;
    font-weight: 900;
}
.social_icons{
    display: flex;
    gap: 10px;
}
.social_icons .icons{
    background: rgba(0, 0, 0, 0.7);
    color: white;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid rgba(225,225,225,0.7);;
    border-radius: 10px;

}




@media only screen and (min-width: 768px) {
    .upper_text{
        
        font-size: 30px;
   }
    .lg_text{
        
        font-size: 38px;
    
    }
    .form input{
        width: 300px;
        
    }
    .form button{
        width: 120px;
    
    }
    
    
}