.container{
    margin: 50px 0 ;
}
.product_heading{
    margin-bottom: 20px;
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
}
.product_heading::after{
    content: "";
    width: 50px;
    height: 3px;
    background: rgba(0,0,0,0.5);
    margin-top: 5px;
    display: block;
}
.products{
    display: flex;
    flex-flow: wrap;
    gap: 10px;
}


@media only screen and (min-width: 768px) {
    .container{
        margin: 75px 0;
    }
    .product_heading{
        margin-bottom: 30px;
        font-size: 30px;
        font-weight: 900;
    }
    .product_heading::after{
        width: 80px;
        height: 5px;
        margin-top: 10px;
    }

    .products{
        gap: 15px;
    }
    
}