.shipping-label-form {
    display: flex;
    flex-direction: column;
    background-color: #333;
    
    padding: 1rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.form-title {
    color: white;
    font-weight: bold;
    font-size: 1.125rem;
}

.form-group {
    margin-top: 1rem;
}

label {
    color: white;
}

input,
textarea,
select {
    width: 100%;
    background-color: #333;
    border: 1px solid #444;
    border-radius: 0.25rem;
    color: white;
    padding: 0.25rem 0.5rem;
}

.form-row {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
}

.form-field {
    flex: 1;
    margin-right: 0.5rem;
}

.form-actions {
    margin-top: 1rem;
    display: flex;
    justify-content: flex-end;
}

button {
    background-color: white;
    color: black;
    border: none;
    border-radius: 0.25rem;
    padding: 0.25rem 0.5rem;
    transition: all 0.2s;
    font-size: 20px;
}

button:hover {
    background-color: #0077b6;
    color: white;
}

@media (min-width: 768px) {
    .shipping-label-form {
        padding: 1.5rem;
    }
    .form-title {
        font-size: 1.5rem;
    }
    
}


@media (min-width: 1024px) {
    .shipping-label-form {
        padding: 2rem;
    }
    .form-title {
        font-size: 1.75rem;
    }
    
}
