.header{
    width: 100%;
    padding: 0 20px;
    background-color: #212121;
    color: white;
    border-bottom: 1px solid rgba(0,0,0,0.2);

  
}
.fixed{
    position: sticky;
    top: 0;
    animation: identifier 0.5s ease-in forwards;
}
.header_content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    max-width: 1200px;
    margin: 0 auto;
}
.center_content{
    font-size: 25px;
    cursor: pointer;
    font-weight: 900;
}
.left_content{
   
    display: none;
   
}
.left_content li{
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
}

.right_content{
    display: flex;
    align-items: center;
    gap: 10px;
}
.right_content svg{
    font-size: 20px;
    cursor: pointer;
}
.right_content .cart_icon{
    position: relative;
}
.right_content .cart_icon span{
    min-width: 20px;
    text-align: center;
    background-color:cadetblue;
    position: absolute;
    top: -5px;
    right: -10px;
    padding: 2px;
    border-radius: 10px;
    line-height: 1;


}


@media only screen and (min-width: 768px) {
    .header{
        padding: 0 40px;
    }
    .header_content{
        height: 80px;
    }
    .center_content{
        font-size: 25px;
        position: absolute;
        left: 50%;
        transform:  translateX(-50%);
    }
    .left_content{
        list-style-type: none;
        display: flex;
        gap: 10px;
    }
    
    .left_content{
        gap: 20px;
    }
    .right_content svg{
        font-size: 30px;
    }
}


@keyframes identifier {
    0%{
        transform: translateY(-80px);
    }
    100%{
        transform: translateY(0);
    }
}