.main_content{
    margin: 30px 0;
}
.design{
    max-width: calc(100% - 20px);
    margin: 0 auto;
}
.title{
    font-size: 18px;
}
.container{
    margin: 20px 0;
}

@media only screen and (min-width: 768px) {
    .main_content{
        margin: 75px 0;
    }
    .design{
        max-width: 1200px;
    }
    .title{
        font-size: 30px;
    }
    .container{
        margin: 50px 0;
    }
    
}