.footer{
    width: 100%;
}
.footer_content{
    max-width: 1200px;
    margin: 0 auto;
    padding: 50px 20px;
    display: flex;
    flex-flow: wrap;
    gap: 20px;
}
.col{
    max-width: 300px;
}

.col .title{
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: bold;
}
.col .text{
    color: rgba(0, 0, 0, 0.5);
    font-size: 14px;
}

.col .contect_icons{
    display: flex;
    margin-bottom: 10px;
    color: rgba(0, 0, 0, 0.5);
}
.col .contect_icons svg{
    flex-shrink: 0;
    font-size: 14px;
    margin-right: 10px;
    margin-top: 5px;
    color: rgba(0, 0, 0, 0.5);
}

.col .contect_text{
    display: block;
    margin-bottom: 10px;
    color: rgba(0, 0, 0, 0.5);
}

.bottom_bar{
    border-top: 1px solid rgba(0,0,0,0.2);
}
.bottom_content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    gap: 20px;
    text-align: center;
}
.bottom_content .text{
    color: rgba(0, 0, 0, 0.5);
}



@media only screen and (min-width: 768px) {
    .footer_content{
        justify-content: space-between;
    }
    .bottom_content{
        flex-direction: row;
        justify-content: space-between;
        padding: 0;
        max-width: 1200px;
        margin: 0 auto;
        height: 60px;
       
    }
}