.products{
    flex-grow: 1;
}
.products .item{
    display: flex;
    gap: 10px;
    padding: 20px 10px;
}
.products .item .imgbox{
    background-color: rgba(0,0,0,0.05);
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    border-radius: 10px;

}
.item .imgbox img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
}
.products .item .item_detail{
    position: relative;
    overflow: hidden;
}
.item .item_detail .item_name{
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1;
    font-size: 18px;
    font-weight: 900;
    margin-bottom: 20px;
}
.item .item_detail .cancel_icon{
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
}


.item .item_detail .quantity_buttons{
    display: flex;
    width: fit-content;
    border: 2px solid gray;
    margin-right: 10px;
    height: 30px;
    margin-bottom: 10px;
}
.item .item_detail  .quantity_buttons span{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    border: 1px solid gray;
    cursor: pointer;
    color: gray;
}
.item .item_detail  .quantity_buttons span:nth-child(2){
    width: 40px;   
}
.item .item_detail .calculation{
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    gap: 10px;
    color: gray;
}
