.single_page_main_content {
    margin: 20px 0;
}

.page_layout {
    max-width: calc(100% - 20px);
    margin: 0 auto;
}

.page_layout .single_page_content {
    display: flex;
    flex-direction: column;
}
.single_page_content .left {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.05);
    flex-shrink: 0;
    border-radius: 30px;
}

 .single_page_content .left img {
    width: 100%;
    display: block;
}

.single_page_content .right {
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}

 .single_page_content .right .product_name {
    font-size: 25px;
    line-height: 20px;
    margin-bottom: 20px;
}
 .single_page_content .right .product_price {
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 20px;
}
.single_page_content .right .product_detail {
    font-size: 15px;
    line-height: 20px;
    margin-bottom: 20px;
    color: gray;
}

.single_page_content .right .cart_buttons {
    display: flex;
    margin-top: 10px;
}

.single_page_content .right .cart_buttons .quantity_buttons {
    display: flex;
    width: fit-content;
    border: 2px solid gray;
    margin-right: 10px;
    height: 40px;
}

 .single_page_content .right .cart_buttons .quantity_buttons span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    border: 1px solid gray;
    cursor: pointer;
    color: gray;
}

.single_page_content .right .cart_buttons .quantity_buttons span:nth-child(2) {
    width: 60px;
}

.single_page_content .right .cart_buttons .add_to_cart {
    border: 0;
    outline: none;
    width: 150px;
    cursor: pointer;
    background-color: gray;
    color: white;

}
.single_page_content .right .cart_buttons .add_to_cart:hover {
    background-color: rgb(115, 111, 111);
}
.single_page_content .right hr {
    margin: 20px 0 20px 0;
}
.single_page_content .right .info .bold {
    font-size: 18px;
    font-weight: 900;
    display: block;
}
.single_page_content .right .info .bold:first-child {
    margin-bottom: 20px;
}
.single_page_content .right .info .bold:first-child span {
    font-size: 14px;
    margin-left: 10px;
    color: gray;
}
.single_page_content .right .info .bold:last-child {
    display: flex;
    font-size: 18px;
    font-weight: 900;
    align-items: center;
}
.single_page_content .right .info .bold:last-child svg {
    font-size: 14px;
    justify-content: center;
    margin-left: 10px;
}

@media only screen and (min-width: 768px) {
    .single_page_main_content {
        margin: 30px 0;
    }

    .page_layout {
        max-width: 1200px;
    }

    .page_layout .single_page_content {
        flex-direction: row;
    }

    .page_layout .single_page_content .left {
        width: 400px;
        height: 400px;
    }

    .page_layout .single_page_content .right {
        padding: 0 30px;
    }

    .single_page_content .right .product_name {
        font-size: 35px;
        line-height: 30px;
    }

    .cart_buttons .quantity_buttons {
        height: 60px;
    }


    .single_page_content .right .info .bold {
        font-size: 25px;
    }

    .single_page_content .right .info .bold:first-child {
        margin-bottom: 30px;
    }

    .single_page_content .right .info .bold:first-child span {
        font-size: 18px;
    }

    .single_page_content .right .info .bold:last-child {
        display: flex;
        font-size: 25px;
    }

    .single_page_content .right .info .bold:last-child svg {
        font-size: 18px;
        color: gray;
    }
}


@media only screen and (min-width: 1024px) {
    .page_layout .single_page_content .left {
        width: 500px;
        height: 500px;
    }
}