.sidebar{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}
.sidebar .background_layer{
    background-color: rgba(0,0,0,0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.sidebar .shoping_detail{
    width: 100%;
    height: 100%;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    z-index: 1;
    transform: translateX(100%);
    animation: identifier 0.3s ease-in forwards;
}

.shoping_detail .sidebar_header{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-bottom: 2px solid gray;
    padding: 20px 15px;

}
.sidebar_header .shoping_cart{
    flex-grow: 1;
    font-size: 20px;
    font-weight: 900;
}
.sidebar_header .close_icon{
    display: flex;
    align-items: center;
    font-weight: 900;
    font-size: 15px;
    gap: 5px;
    cursor: pointer;
}

.sidebar_header .close_icon:hover{
   opacity: 0.5;
}

.shoping_detail .empty{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 120px;
    gap: 10px;
}
.shoping_detail .empty svg{
   font-size: 120px;
   color: gray;
   opacity: 0.3;
   margin-bottom: 10px;
}
.shoping_detail .empty span{
    font-size: 25px;
    color: gray;
    opacity: 0.5;
    margin-bottom: 10px;
}
.shoping_detail .empty .shop_again{
    border: 2px solid rgb(81, 78, 78);;
    outline: none;
    margin: 20px;
    background-color: gray;
    height: 50px;
    color: black;
    font-weight: 900;
    padding: 0 20px;
    font-size: 15px;
}
.shoping_detail .empty .shop_again:hover{
    background-color: rgb(81, 78, 78);
}

.shoping_detail .sidebar_footer{
    border-top: 2px solid gray;
}

.shoping_detail .sidebar_footer .total{
    border-bottom: 2px solid gray;
    display: flex;
    justify-content: space-between;
    padding: 5px 20px;
    height: 50px;
    font-size: 20px;
    align-items: center;
    font-weight: 900;
    color: rgb(84, 82, 82);
}

.shoping_detail .sidebar_footer .button{
    height: 50px;

}
.sidebar_footer .button .done_payment{
    border: 2px solid rgb(81, 78, 78);;
    outline: none;
    margin: 20px;
    background-color: gray;
    height: 50px;
    width: 100%;
    color: black;
    font-weight: 900;
    padding: 0 20px;
    font-size: 25px;
}





@media only screen and (min-width: 768px) {
    .sidebar .shoping_detail{
        width: 350px;
        
    }
}


@keyframes identifier {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX();
    }
}