.main_cart {
    width: calc(50% - 10px);
    margin-bottom: 20px;
}

.main_cart .thumbnail {
    width: 100%;
    height: 180px;
    background-color: rgba(0, 0, 0, 0.05);
    margin-bottom: 10px;
    padding: 20px;
    display: flex;
    align-items: center;
}

.thumbnail img {
    display: block;
    width: 100%;
    transition: all 0.3s ease-in;
}
.thumbnail img:hover{
    transform: scale(1.2);
    overflow: hidden;
}

.detail .product_name {
    font-size: 15px;
    display: block;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-bottom: 5px;
    white-space: nowrap;
}


@media only screen and (min-width: 768px) {
    .main_cart {
        width: calc(25% - 15px);
    }

    .thumbnail {
        height: 200px;
    }
    .detail .product_name{
        font-size: 18px;
    }
}


