.category{
    margin: 25px 0;

}
.category .item{
    display: flex;
    flex-flow: wrap;
    gap: 10px;
}
.item .item_pic{
    width: calc(50% - 5px);
    overflow: hidden;
    background-color: black;
    cursor: pointer;
}
.item .item_pic img{
    width: 100%;
    transition: all ease 0.3s;
    display: flex;
    justify-content: center;
}
.item .item_pic img:hover{
    transform: scale(1.2);
}
@media only screen and (min-width: 768px) {
    .category{
        margin: 50px 0;
    }
    .item .item_pic{
        width: calc(25% - 10px);
       
    }
}