.banner {
    padding: 40px 0;
    position: relative;
    background: linear-gradient(90deg, rgba(185, 195, 207, 1) 0%, rgba(120, 124, 130, 1) 0%, rgba(163, 162, 180, 1) 1%, rgba(109, 111, 113, 1) 28%, rgba(151, 152, 154, 1) 62%, rgba(140, 142, 143, 1) 85%, rgba(90, 104, 107, 1) 97%, rgba(71, 79, 80, 1) 100%);
    height: 500px;
}

.content {
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column-reverse;
    align-items: center;
    position: relative;
    margin: 0 auto;
}

.banner_img {
    position: relative;
    z-index: 10;
    width: 250px;
    margin: 0 auto;
}

.text_content {
    color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.loader{
    border-right: 3px solid;
    width: 0px;
    animation: typewriter 2s steps(10) infinite alternate, blink 0.3s  infinite;
}

.loader h1 {
    font-size: 40px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 20px;
     background: linear-gradient(to right, #6d716f, #31373c);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


@keyframes typewriter {
    0% {
        width: 0px;
    }

    100% {
        width: 150px;
    }
}

@keyframes blink {
    0% {
        border-right-color: rgba(255, 255, 255, .75);
        ;
    }

    100% {
        border-right-color: transparent;
    }
}

.text_content p {
    max-width: 300px;
    font-size: 15px;
    line-height: 25px;
    margin-bottom: 20px;

}

.button {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.button .bannar_btn {
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 600;
    border: 2px solid white;
    padding: 5px 8px;
    cursor: pointer;
}

.button .btn2 {
    background: white;
    color: black;
}

.button .bannar_btn:hover {
    opacity: 0.6;
}

@media only screen and (min-width: 768px) {
    .banner {
        height: calc(100vh - 80px)
    }

    .content {
        flex-direction: row;
        max-width: 1200px;
    }

    .banner_img {
        width: 400px;
        margin-right: 30px;
        margin-top: 50px;
        margin-bottom: 0;
        ;
    }

    .text_content {
        position: absolute;
        left: 50px;
        top: 50%;
        transform: translateY(-50%);

    }

    .text_content h1 {
        font-size: 100px;
    }

    .text_content p {
        max-width: 300px;
        font-size: 18px;
        line-height: 30px;
    }

    .button .bannar_btn {
        padding: 10px 20px;
    }

    @keyframes typewriter {
        0% {
            width: 0px;
        }
    
        100% {
            width: 110%;
        }
    }
}

@media only screen and (min-width: 1000px) {
    .text_content h1 {
        font-size: 180px;
    }

    .text_content p {
        max-width: 500px;
    }

    .banner_img {
        width: 500px;
    }
}

@media only screen and (min-width: 1536px) {
    .banner_img {
        width: 600px;
        margin: 0;
    }

    .text_content {
        left: 0;
    }
}