.search{
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 99;
    top: 0;
    right: 0;
    background-color:rgba(225,225,225,0.7);
    transform: translateX(100%);
    animation: identifier 0.5s ease-in-out forwards;
   

}
.search_field{
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    padding: 20px 50px;
    border-bottom: 2px solid gray;
}
.search_field input{
    width: 100%;
    height: 60px;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    outline: none;
    border: none;
    background-color:rgba(225,225,225,0.7);
    color: white;
}
.search_field svg{
    position: absolute;
    font-size: 25px;
    height: 60px;
    right: 50px;
    display: flex;
    align-items: center;
}
.search_content{
    max-width: calc(100% - 50px);
    margin: 0 auto;
}

.search_content .search_result{
    height: calc(100vh - 200px);
    overflow: auto;
    margin: 20px 0;
}
.search_result .search_items{
    display: flex;
    align-items: center;
    gap: 20px;
    cursor: pointer;
    padding: 10px 0;
}
.search_items .img_container{
    background-color: rgba(14, 14, 14, 0.5);
    width: 80px;
    height: 80px;
    flex-shrink: 0;
    border-radius: 10px;
    margin-left: 10px;

}
.img_container img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
  
}
.search_item_detail{
    overflow: hidden;
}
.search_item_detail .item_name{
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 2;
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 700;
}
.search_item_detail .product_description{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    line-height: 1;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
    color: gray;
}

@media only screen and (min-width: 768px) {
    .search{
        width: 50%;
    }
    .search_content{
        max-width: 600px;
    }
    .search_content .search_result{
        height: calc(100vh - 250px);
        overflow: auto;
        margin: 20px 0;
    }
}



@keyframes identifier {
    0%{
        transform: translateX(100%); 
    }
    100%{
        transform: translateX(0);
    }
}
